<template>
  <div id="app">
    <!-- <Header></Header> -->
    <router-view></router-view>
   <Footer v-show="$root.$data.bFoot"></Footer>
  </div>
</template>

<script>

import Footer from './components/Footer'
// import Header from './components/Header'

export default{
 name:'aPp',
  components:{
   Footer
  },
  watch:{
    handler(to){
      let path=to.path;
      console.log(path)
      if(/home|follow|detail/.test(path)){
        this.$root.$data.bFoot=true
      }
      if(/login|reg|user//test(path)){
        this.$root.$data.bFoot=false
      }
    }
  }

}
</script>

