<template>
  <div class="content">
    <p class="fhbtn">
      <a href="javascript:;" onclick="window.history.go(-1)"></a>
    </p>
    <h1></h1>
    <div class="login-box">
      <p class="lsolid"></p>
      <div class="login">
       <router-link to="login">登录</router-link>
       <span></span>
       <router-link to="/reg">注册</router-link>
      </div>
      <p class="rsolid"></p>
    </div>
    <ul>
      <li class="lifirst">
        <input type="text" value="" v-model="username">
        <span>帐号</span>
      </li>
      <li>
        <input type="text" value="" v-model="password">
        <span>密码</span>
      </li>
    </ul>
    <div class="footbox">
      <input type="button" value="登 录" class="login-btn" @click="login">
      <a href="javascript:;" class="tishi">忘记密码？</a>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      username:'',
      password:'',
      mess:''
    }

  },
  methods:{
    login(){
      axios({
        url:'/api/login',
        method:'post',
        data:{username:this.username,password:this.password,save:true}
      }).then(
        res=>{
          if(res.data.err===0){
            console.log(res.data)
            this.$router.push('/user')
          }else{
            console.log(res.data)
            this.mess=res.data.mess
          }
        }
      )
    }
  }
}
</script>

