<template>
    <div class="follow">
      <Search></Search>
      <Goods :goods="goods" :dataName="'follow'"></Goods>
    </div>
</template>
<script>
import Search from '../components/Search'
import Goods from '../components/Goods'
export default {
 
   data(){
    return{
      goods:[]
    }
  },
 components:{Search,Goods},
  mounted(){
    axios({
      url:'/api/follow',
     
     params:{_page:1,_limit:11}
    }).then( 
    //  console.log(dataName),
      res=>this.goods=res.data.data                                                                    
    )
    
  }
}
</script>

